<template>
  <div class="container">
    <h1>Notas</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{ mensaje.texto }}
    </b-alert>

    <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
        <h3>Editar Nota</h3>
        <input type="text" class="form-control my-2" placeholder="Nombre" v-model="notaEditar.nombre">
        <input type="text" class="form-control my-2" placeholder="Descripcin" v-model="notaEditar.descripcion">
        
        <button type="submit" class="btn btn-success my-1 mx-2">Editar</button>
        <button type="submit" class="btn btn-secondary my-1" @click="editar = false">cancelar</button>
        
    </form>

    <form @submit.prevent="agregarNota()" v-if="!editar">
        <h3>Agregar nueva nota</h3>
        <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.nombre">
        <input type="text" class="form-control my-2" placeholder="Descripcin" v-model="nota.descripcion">
        <div class="d-grid gap-2">
            <button type="submit" class="btn btn-success my-2">Agregar</button>
        </div>
    </form>

    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nombre</th>
          <th scope="col">Descripcion</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          <th scope="row">{{ item._id }}</th>
          <td>{{ item.nombre }}</td>
          <td>{{ item.descripcion }}</td>
          <!-- <td> <b-button @click="alerta()">Accin</b-button> </td> -->
          <td>
            <b-button class="btn-danger btn-sm " @click="eliminarNota(item._id)">Eliminar</b-button>
            <b-button class="btn-primary btn-sm m-1" @click="activarEdicion(item._id)">Editar</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      notas: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      mensaje: {color: '', texto:''},
      nota: {nombre:'', descripcion:''},
      notaEditar: {},
      editar: false
    };
  },
  methods: {
      alerta(){
          this.mensaje.color = 'success'
          this.mensaje.texto = 'Probando alerta'
          this.showAlert()
      },
    async listarNotas() {
      //con vue axios de puede hacer los llamados de manera directa
      //la base_url tambien esta configurada en el main.js
      //this.axios.get("/nota").then((res) => {
        //console.log(res.data);
        //this.notas = res.data;
      //});
      //sin axios
      try {
          const res = await fetch('https://bakend-mevn.herokuapp.com/api/nota')
          const dataDB = await res.json()
          this.notas = dataDB
          console.log(dataDB)
      } catch (error) {
          console.log(error)
      }
    },
    agregarNota(){
        this.axios.post('/nueva-nota', this.nota)
        .then(res=> {
            this.notas.push(res.data)
            this.nota.nombre = ''
            this.nota.descripcion = ''
            this.mensaje.color = 'success'
            this.mensaje.texto = 'Nota agregada'
            this.showAlert()
        }).catch(err => {
            console.log(err.response)
            if(err.response.data.error.errors.nombre.message){
                this.mensaje.texto = err.response.data.error.errors.nombre.message
            }else{
                this.mensaje.texto = 'Error del sistema'
            }
            this.mensaje.color = 'danger'
            
            this.showAlert()
        })
    },
    eliminarNota(id){
      this.axios.delete(`/nota/${id}`)
        .then(res => {
          const index = this.notas.findIndex(item => item._id === res.data._id)
          //splice borra la candidad de elementos indicados que coincidan con el este index
          this.notas.splice(index, 1)
          this.mensaje.color = 'success'
          this.mensaje.texto = 'Nota eliminada'
          this.showAlert()
        }).catch(err=>{

        })
    },
    activarEdicion(id){
      this.editar = true
      this.axios.get(`/nota/${id}`)
        .then(res => {
          this.notaEditar = res.data
        })
    },
    editarNota(item){
      this.axios.put(`/nota/${item._id}`, item)
        .then(res => {
          const index = this.notas.findIndex(n => n._id === res.data._id)
          this.notas[index].nombre = res.data.nombre
          this.notas[index].descripcion = res.data.descripcion
          this.mensaje.color = 'success'
          this.mensaje.texto = 'Nota Editada'
          this.showAlert()
          this.editar = false
        })
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    }
  },

  created() {
    this.listarNotas();
  },
};
</script>

